<script>

export default {
  name: 'HelloWorld',
  mounted() {
    var preloader = document.getElementById('preloader')
    if(preloader){preloader.classList.add('preloader-hide');}
    
    this.btn_new_html = $("#btn_new").html();
  },
  created() {
    window.addEventListener('scroll', this.handleScroll);
  },
  destroyed () {
    window.removeEventListener('scroll', this.handleScroll);
  },
  data () {
    return {
        map: null,
        oldScroll: 0,
        btn_new_html: '',
        prev_driection: true,
    }
  }, methods: {
      call: function() {
          console.log("call");
      },
      goCourse: function() {
          location.href='/course';
      },
      handleScroll: function(event) {
          var current_driection = (this.oldScroll > window.pageYOffset);
          if (current_driection != this.prev_driection && current_driection == true) {
              $("#btn_new").html(this.btn_new_html.replace('</i>', '&nbsp; </i>'));
              $("#btn_new").css("width", "auto");
          } else if (current_driection != this.prev_driection && current_driection == false) {
              $("#btn_new").html(this.btn_new_html);
              $("#btn_new").css("width", "48px");
          }
          this.prev_driection = current_driection;
          this.oldScroll = window.pageYOffset;
      }
  }

  
}


</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.wedive-chip {padding-left: 16px;margin: 0px 4px 0px 0px;margin-bottom: 4px;font-family: 'Noto Sans Korean'}
