            {title: " ", desc: "", star: 4.1, price_index: 2, feature: ", ", img: '/static/images/shop1/diving/test7.jpg', position: {lat: 33.24194725508795, lng: 126.5616725869943}},
            {title: "  ", desc: "", star: 4.2, price_index: 3, feature: ", , , ", img: '/static/images/shop1/diving/test8.jpg', position: {lat: 33.24088391439924, lng: 126.5628795809329}},
            {title: "40", desc: "", star: 4.3, price_index: 2, feature: ", ", img: '/static/images/shop1/diving/test9.jpg', position: {lat: 33.242485636047576, lng: 126.5623109526933}},
            {title: " ", desc: "", star: 4.7, price_index: 2, feature: ", ", img: '/static/images/shop1/diving/test10.jpg', position: {lat: 33.244246055136834, lng: 126.5671937429616}}
        ],
    }
  }, methods: {
